import Peer from "peerjs";
import { createContext, useEffect, useReducer, useState } from "react";
import { useNavigate } from "react-router-dom";
import socketIOClient from "socket.io-client";
import { v4 as uuidV4 } from "uuid";
import { peersReducer } from "./PeerReducer";
import { addPeerAction, removePeerAction } from "./PeerActions";

const WS = "http://localhost:8080";

export const RoomContext = createContext<null | any>(null);

const ws = socketIOClient(WS);

export const RoomProvider = ({ children }: any) => {
  const navigate = useNavigate();
  const [me, setMe] = useState<Peer>();
  const [stream, setStream] = useState<MediaStream>();
  const [peers, dispatch] = useReducer(peersReducer, {});
  const [screenSharingId, setScreenSharingId] = useState("");

  const enterRoom = ({ roomId }: any) => {
    navigate(`/room/${roomId}`);
  };

  const getUsers = ({ participants }: { participants: string[] }) => {
    console.log("[participants] ", { participants });
  };
  const removePeer = (peerId: string) => {
    dispatch(removePeerAction(peerId));
  };
  const switchScreen = (stream: MediaStream) => {
      setStream(stream);
      setScreenSharingId(me?.id ?? "");
  };
  const shareScreen = () => {
    if (screenSharingId) {
      navigator.mediaDevices
        .getUserMedia({ video: true, audio: true })
        .then(switchScreen);
    } else {
      navigator.mediaDevices.getDisplayMedia({}).then(switchScreen);
    }
    
  };

  function collectQoSStats(peerConnection: RTCPeerConnection) {
    console.log('llego')
    setInterval(() => {
      peerConnection.getStats(null)
        .then((stats) => {
          stats.forEach((report) => {
            if (report.type === 'outbound-rtp' && report.kind === 'video') {
              console.log('Video Outbound RTP Stats:');
              console.log(`Bytes Sent: ${report.bytesSent}`);
              console.log(`Packets Sent: ${report.packetsSent}`);
            }
            if (report.type === 'inbound-rtp' && report.kind === 'video') {
              console.log('Video Inbound RTP Stats:');
              console.log(`Bytes Received: ${report.bytesReceived}`);
              console.log(`Packets Lost: ${report.packetsLost}`);
              console.log(`Jitter: ${report.jitter}`);
              console.log(`RTT: ${report.roundTripTime}`);
            }
            if (report.type === 'candidate-pair' && report.state === 'succeeded') {
              console.log('Network Stats:');
              console.log(`Current Round Trip Time: ${report.currentRoundTripTime}`);
              console.log(`Available Outgoing Bitrate: ${report.availableOutgoingBitrate}`);
              console.log(`Available Incoming Bitrate: ${report.availableIncomingBitrate}`);
            }
          });
        })
        .catch(err => console.error('Error getting stats:', err));
    }, 5000); // Recolecta estadísticas cada 5 segundos
  }
  
  useEffect(() => {
    const meId = uuidV4();
    // Id que Peer le asigna a cada usuario
    const peer = new Peer(meId);
    setMe(peer);
    try {
      // Funcion para acceder a la camara y microfono
      navigator.mediaDevices
        .getUserMedia({ video: true, audio: true })
        .then((stream) => {
          setStream(stream);
        });
    } catch (error) {
      console.error(error);
    }
    ws.on("room-created", enterRoom);
    ws.on("get-users", getUsers);
    ws.on("user-disconnected", removePeer);

    return () => {
      ws.off("room-created");
      ws.off("get-users");
      ws.off("user-disconnected");
      ws.off("user-joined");
      me?.disconnect();
    };
  }, [ws]);

  useEffect(() => {
    if (!me) return;
    if (!stream) return;

    ws.on("user-joined", ({ peerId }) => {
      console.log("[user-joined]: ", { peerId });
      const call = me.call(peerId, stream);
      call.on("stream", (peerStream) => {
        dispatch(addPeerAction(peerId, peerStream));
        // Llamar a la función para recolectar estadísticas de QoS
        // collectQoSStats(call.peerConnection);
      });
    });

    me.on("call", (call) => {
      call.answer(stream);
      console.log("[call]: ", call);
      call.on("stream", (peerStream: MediaStream) => {
        dispatch(addPeerAction(call.peer, peerStream));
        // collectQoSStats(call.peerConnection);
      });
    });

    return () => {
      ws.off("user-joined");
    };
  }, [ws, me, stream]);

  return (
    <RoomContext.Provider value={{ ws, me, stream, peers, shareScreen }}>
      {children}
    </RoomContext.Provider>
  );
};
